<template>
  <div id="courseware">
    <head-top title="课件"></head-top>
    <van-pull-refresh v-model="loading" @refresh="getDataList" class="pull_fresh">
      <div class="main">
        <div v-for="(item, index) in dataList" :key="index" class="item" @click="download(item)">
          <div class="fL">
            <i class="iconfont iconword"></i>
            <span
              class="title time"
            >{{item.Value['MaterialName'] + '.' + item.Value['MaterialFileExt']}}</span>
          </div>
          <div class="size time fR">
            <span
              v-if="item.Value['MaterialFileSize']>1024*1024"
            >{{ Math.round( item.Value['MaterialFileSize']/ 1024.0/1024 )}} MB</span>
            <span v-else>{{ Math.round( item.Value['MaterialFileSize']/ 1024) }} KB</span>
          </div>
        </div>

        <van-loading v-if="loading" type="spinner" color="#1989fa">加载中...</van-loading>
        <r-lack-data v-else-if=" !dataList || dataList.length === 0"></r-lack-data>
        <!-- <embed :src="previewUrl" type="application/pdf" width="100%" height="100%" />
        <object :src="previewUrl" width="100%" height="100%">
          This browser does not support PDFs. Please download the PDF to view it:
          <a :href="previewUrl">Download PDF</a>
        </object>-->
      </div>
    </van-pull-refresh>
  </div>
</template>

<script>
import { Loading } from "vant";
import headTop from "@/components/headTop";
import { FuncType, sendToAPP } from "@/utils/interFlow";
export default {
  name: "",
  components: { headTop, [Loading.name]: Loading },
  props: {},
  data() {
    return {
      dataList: [],
      // previewUrl: "file:///D:/Program%20Files%20(x86)/Tencent/WeChat/WeChat%20Files/snowmooning/FileStorage/File/2019-09/IOS_APP%E9%9C%80%E6%B1%82%E8%AF%B4%E6%98%8E%E4%B9%A6(1).pdf",
      loading: false
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      this.getDataList();
    });
  },
  methods: {
    getDataList() {
      this.dataList = [];
      this.loading = true;
      this.$mainHttp.courseware
        .getList({
          courseID: this.$store.getters.courseID
        })
        .then(res => {
          if (res.Result && res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.dataList.push(item);
            });
          }
          this.loading = false;
        })
        .catch(err => {
          this.loading = false;
        });
    },
    download(item) {
      // this.previewUrl = this.$mainHttp.resourse.file(item.Value["MaterialID"]);
      let fileUrl = this.$mainHttp.resourse.file(item.Value["MaterialID"]);
      if (!window.webkit) {
        window.location.href = fileUrl;
        this.$toast("下载开始");
      } else {
        sendToAPP(FuncType.downloadFile, fileUrl)
          .then(res => {})
          .catch(err => {});
      }
    }
  },
  computed: {},
  watch: {}
};
</script>

<style lang='less' scoped>
#courseware {
  width: 100%;
  height: 100%;
  .main {
    width: 100%;
    background: #ffffff;
    max-height: calc(100vh - 4.5rem);
    overflow-y: auto;
    .item {
      overflow: hidden;
      width: 92vw;
      height: 6vh;
      line-height: 6vh;
      margin: 0 auto;
      border-bottom: 1px solid #dddddd;
      span,
      i {
        display: inline-block;
      }
      .iconfont {
        font-size: 1.7rem;
        color: blue;
      }
    }
  }

  .pull_fresh {
    /deep/ .van-pull-refresh__track {
      height: calc(100vh - 4.5rem);
    }
  }
}
</style>


